  .banner-container {
    display: flex;
    justify-content: space-between;
    margin: 20px;
    column-gap: 50px;
  }
  
  .banner-card {
    width: 48%;
    height: 200px;
    border-radius: 20px;
    /* display: flex; */
    justify-content: left;
    align-items: top;
    color: white;
    font-family: 'Arial', sans-serif;
    text-align: left;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
  }
  
  .pink-gradient {
    background: linear-gradient(to top right, #bc3295, #f15b8e);
  }
  
  .blue-gradient {
    background: linear-gradient(to top right, #2c5dc8, #2fcbf0);
  }
  
  .banner-card h2 {
    font-weight: 900;
    font-size: 60px;
    margin: 0;
    margin-top: -15px;
  }
  
  .banner-card p {
    font-size: 26px;
    color: rgba(255, 255, 255, 0.7);
    margin-top: -10px;
    margin-left: 2px;
  }
  
  .banner-card svg {
    font-size: 30px;
    margin-bottom: -97px;
  }