  .player {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 90%;
    /* height: 10%;
    background-color: #ff0066; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 18px 20px;
    color: white;
  }
  
  .player-info {
    display: flex;
    align-items: center;
  }
  
  .player-info svg {
    margin-top: -30px;
    margin-left: 50px;
  }
  
  .player-cover {    
    width: 55px;
    height: 55px;
    border-radius: 50%;
    border-color: #fff;
    border-width: 5px;
    box-shadow: 0 0 0 3px #ffffff51;
    margin-left: 30px;
    margin-right: 30px;
    background-color: #fff;
  }
  
  .player-details {
    display: flex;
    flex-direction: column;
  }
  
  .player-details span:first-child {
    font-weight: bold;
  }
  
  .player-details span:last-child {
    font-size: 12px;
    color: #ddd;
  }
  
  .player-controls {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .player-controls svg {
    font-size: 24px;
    margin: 0 10px;
    cursor: pointer;
  }
  
  .player-controls svg:hover {
    opacity: 0.8;
  }
  
  .player-progress {
    display: flex;
    align-items: center;
  }
  
  .player-progress span {
    margin: 0 10px;
  }
  
  .progress-bar {
    accent-color: rgb(255, 255, 255);
    /* background-color: rgba(255, 255, 255, 0.396); */
    /* background-color: blue; */
    color: rgba(0, 0, 0, 0.2);
    width: 600px;
    -webkit-appearance: none;
    /* background: linear-gradient(90deg, #B3B3B3 80%, #535353 80%); */
    outline: none;
    height: 4px;
    border-radius: 12px;
    cursor: pointer;
    background: rgba(0, 0, 0, 0.2);
    /* -webkit-appearance: none;
    width: 100%;
    background: linear-gradient(90deg, #B3B3B3 80%, #535353 80%);
    outline: none;
    height: 4px;
    border-radius: 12px;
    cursor: pointer; */
  }

  .progress-bar:focus {
    outline: none;
  }
  
  .progress-bar::-webkit-slider-thumb {
    /* -webkit-appearance: none;
    appearance: none;
    width: 0px;
    height: 0px; */
    background: rgb(0, 0, 0);
  }

  .progress-bar:hover::-webkit-slider-thumb {
    /* width: 10px;
    height: 10px; */
    border-radius: 50%;
  }
  
  .volume-bar {
    accent-color: rgb(255, 255, 255);
    /* background-color: rgba(255, 255, 255, 0.396); */
    /* background-color: blue; */
    color: rgb(255, 255, 255);
    width: 160px;
    -webkit-appearance: none;
    /* background: linear-gradient(90deg, #B3B3B3 80%, #535353 80%); */
    outline: none;
    height: 4px;
    border-radius: 12px;
    cursor: pointer;
    background: rgba(255, 255, 255);
    /* -webkit-appearance: none;
    width: 100%;
    background: linear-gradient(90deg, #B3B3B3 80%, #535353 80%);
    outline: none;
    height: 4px;
    border-radius: 12px;
    cursor: pointer; */
  }

  .volume-bar:focus {
    outline: none;
  }
  
  .volume-bar::-webkit-slider-thumb {
    /* -webkit-appearance: none;
    appearance: none;
    width: 0px;
    height: 0px; */
    background: rgb(0, 0, 0);
  }

  .volume-bar:hover::-webkit-slider-thumb {
