.user-details-container {
  display: flex;
  justify-content: flex-end;
  padding: 10px 0;
  background: rgb(24, 24, 24);
}

.user-image {
  border-radius: 50%;
  height: 30px;
}

.user-name {
  color: #fff;
  font-size: 14px;
  position: relative;
  top: 5px;
  margin-left: 10px;
}
  
.user-profile {
  display: flex;
  align-items: center;
  margin-left: 24px;
  margin-bottom: 20px;
}
  
.user-info span {
  font-size: 20px;
  font-weight: bolder;
  letter-spacing: .1rem;
}

.profile-pic {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
  background-color: #fff;
}
