  .tracklist {
    position: absolute;
    margin: 20px;
    width: 48%;
    height: 130%;
    overflow: hidden;
  }
  
  .tracklist h3 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .tracklist ul {
    position: relative;
    list-style-type: none;
    padding: 0;
    margin: 0;
    height: 95%;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  
  .track-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    transition: background-color 0.2s ease-in-out;
  }
  
  .track-item:hover {
    background-color: #f0f0f0;
  }
  
  .track-cover {
    width: 50px;
    height: 50px;
    border-radius: 8px;
    margin-right: 15px;
  }
  
  .track-info {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  
  .track-info span {
    display: block;
  }
  
  .track-info span:first-child {
    font-weight: 600;
    width: 35%;
  }
  
  .track-info span:nth-child(2) {
    width: 30%;
  }
  
  .track-info span:nth-child(3) {
    width: 15%;
    text-align: right;
  }
  
  .track-info span:nth-child(4) {
    width: 10%;
    text-align: right;
  }
  
  .track-info span:last-child {
    color: #888;
    width: 10%;
    text-align: center;
  }