  .foryoulist {
    position: absolute;
    margin: 20px;
    width: 48%;
    height: 130%;
    overflow: hidden;
    right: 0;
  }
  
  .foryoulist h3 {
    font-size: 20px;
    font-weight: 600;
    color: #333;
    margin-bottom: 10px;
  }
  
  .foryoulist ul {
    position: relative;
    list-style-type: none;
    padding: 0;
    margin: 0;
    height: 100%;
    overflow: hidden;
    display: flex;
    column-gap: 19px;
    flex-wrap: wrap;
    justify-content: flex-start;
    list-style: none;
  }
  
  .foryou-item {
    width: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    text-align: center;
    color: #333;
    font-size: 14px;
    cursor: pointer;
    transition: transform 0.2s;
  }
  
  .foryou-item:hover {
    /* background-color: #f0f0f0; */
    transform: scale(1.05);
  }
  
  .foryou-cover {
    width: 50px;
    height: 50px;
    border-radius: 8px;
    margin-right: 15px;
    width: 100%;
    height: 150px;
    background-color: #ddd;
    border-radius: 10px;
    margin-bottom: 10px;
    object-fit: cover;
  }
  
  .foryou-info {
    /* display: flex; */
    text-align: left;
    justify-content: space-between;
    width: 100%;
  }
  
  .foryou-info span {
    display: block;
  }
  
  .foryou-info span:first-child {
    font-weight: 600;
    margin-bottom: 4px;
  }
  
  .foryou-info span:last-child {
    color: #888;
    color: #777;
    font-size: 13px;
  }

