.side-menu-container {
  padding: 20px;
  color: rgb(96, 96, 96);
  margin: 0;
  letter-spacing: .1rem;
}

.user-library-header {
  font-weight: bolder;
  text-transform: uppercase;
  font-family: 'Proxima Thin', Georgia, sans-serif;
  font-size: 13px;
  margin: 0;
  padding: 0 0 5px 0;
  letter-spacing: 1px;
}

.side-menu-item {
  list-style-type: none;
  line-height: 42px;
  font-size: 16px;
  width: 140px;
  cursor: pointer;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.side-menu-item:hover {
  color: #000000;
}

.side-menu-item svg {
  padding-right: 18px;
}

.radio {
  margin-bottom: 20px;
}

.side-menu-container .active {
  color: rgb(0, 0, 0);
  font-weight: bold;
}

